<template><markdown-wrapper><h1 id="getting-started">Getting Started <a class="header-anchor" href="#getting-started" aria-hidden="true">&#128279;</a></h1>
<p><strong>uiv</strong> is a Bootstrap 3 component lib implemented by Vue 2.</p>
<ul>
<li><strong>Lightweight</strong>
<ul>
<li>All components <strong>~20KB</strong> Gziped.</li>
<li>No extra CSS file.</li>
<li>Individually import supported.</li>
</ul>
</li>
<li>IE 9+ &amp; modern browsers supported.</li>
<li><strong>SSR</strong> (server-side rendering) supported.</li>
<li>All env supported:
<ul>
<li>UMD build <code v-pre>uiv.min.js</code> can be used in all environments (including browser)</li>
<li>ES Module build <code v-pre>uiv.esm.js</code> is for modern bundlers like <a href="https://webpack.js.org">webpack 2</a> or <a href="https://rollupjs.org">rollup</a></li>
<li>CommonJS build <code v-pre>uiv.common.js</code> is for older bundlers like <a href="http://browserify.org">browserify</a> or <a href="https://webpack.github.io">webpack 1</a>.</li>
</ul>
</li>
</ul>
<p><ins class="adsbygoogle"
     style="display:block; text-align:center;"
     data-ad-layout="in-article"
     data-ad-format="fluid"
     data-ad-client="ca-pub-4714899946256166"
     data-ad-slot="4603582855"></ins></p>
<h2 id="install">Install <a class="header-anchor" href="#install" aria-hidden="true">&#128279;</a></h2>
<p>If you are using module bundlers such as Webpack, you can directly include package into your project via:</p>
<p><strong>NPM</strong>:</p>
<pre v-pre><code v-pre class="language-bash">$ npm install uiv --save
</code></pre>
<p>or <strong>Yarn</strong>:</p>
<pre v-pre><code v-pre class="language-bash">$ yarn add uiv
</code></pre>
<p>Then register uiv components and directives all at once in your app's entry:</p>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-comment">// main.js</span>
<span class="hljs-keyword">import</span> <span class="hljs-string">'bootstrap/dist/css/bootstrap.min.css'</span>

<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> * <span class="hljs-keyword">as</span> uiv <span class="hljs-keyword">from</span> <span class="hljs-string">'uiv'</span>

Vue.use(uiv)
</code></pre>
<p>That's it. Happy coding!</p>
<h3 id="no-conflict">No conflict <a class="header-anchor" href="#no-conflict" aria-hidden="true">&#128279;</a></h3>
<p>All components &amp; directives will be installed with no prefix by default, you can add any prefix to them to avoid conflicts with other libs if needed.</p>
<p>For example:</p>
<pre v-pre><code v-pre class="language-javascript">Vue.use(uiv, {<span class="hljs-attr">prefix</span>: <span class="hljs-string">'uiv'</span>})
</code></pre>
<p>Results in:</p>
<ul>
<li>Components such as <code v-pre>&lt;alert&gt;</code> becomes <code v-pre>&lt;uiv-alert&gt;</code></li>
<li>Directives such as <code v-pre>v-tooltip</code> becomes <code v-pre>v-uiv-tooltip</code></li>
<li>Global methods such as <code v-pre>$alert</code> becomes <code v-pre>$uiv_alert</code></li>
</ul>
<h2 id="import-individually">Import individually <a class="header-anchor" href="#import-individually" aria-hidden="true">&#128279;</a></h2>
<p>If you don't want all of the components for some reason (e.g. to save the bundle size), you can also import them individually.</p>
<h3 id="example">Example <a class="header-anchor" href="#example" aria-hidden="true">&#128279;</a></h3>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> { Alert } <span class="hljs-keyword">from</span> <span class="hljs-string">'uiv'</span>
<span class="hljs-comment">// or</span>
<span class="hljs-comment">// import Alert from 'uiv/dist/Alert'</span>

<span class="hljs-keyword">new</span> Vue({
  <span class="hljs-attr">components</span>: {
    Alert
  }
})
</code></pre>
<h2 id="browsers">Browsers <a class="header-anchor" href="#browsers" aria-hidden="true">&#128279;</a></h2>
<p>You can load &amp; install uiv package directly in browsers. For example:</p>
<pre v-pre><code v-pre class="language-html"><span class="hljs-comment">&lt;!-- Remember to import Vue and Bootstrap CSS file before this! --&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"//unpkg.com/uiv"</span>&gt;</span><span class="undefined"></span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<p>This will simply load the latest version of <code v-pre>uiv.min.js</code> into your page. For detail usages (e.g. load specify version, IMPORTANT in production mode) and different CDN providers, you can visit:</p>
<ul>
<li><a href="https://unpkg.com">https://unpkg.com</a></li>
<li><a href="https://www.jsdelivr.com/">https://www.jsdelivr.com</a></li>
<li><a href="https://cdnjs.com/libraries/uiv">https://cdnjs.com/libraries/uiv</a></li>
</ul>
<h3 id="complete-usage-example">Complete usage example <a class="header-anchor" href="#complete-usage-example" aria-hidden="true">&#128279;</a></h3>
<pre v-pre><code v-pre class="language-html"><span class="hljs-comment">&lt;!-- index.html --&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">html</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">head</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">link</span> <span class="hljs-attr">rel</span>=<span class="hljs-string">"stylesheet"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">script</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text/javascript"</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"//vuejs.org/js/vue.min.js"</span>&gt;</span><span class="undefined"></span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">script</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text/javascript"</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"//unpkg.com/uiv/dist/uiv.min.js"</span>&gt;</span><span class="undefined"></span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">head</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">body</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"app"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tabs</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">tab</span>&gt;</span>Tab content 1.<span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">tab</span>&gt;</span>Tab content 2.<span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">tabs</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="actionscript">
  <span class="hljs-comment">// No need to install uiv, we already do this for you after script loaded.</span>
  <span class="hljs-comment">// Define `window.__uiv_options` before script loaded if you need install options.</span>
  <span class="hljs-keyword">new</span> Vue().$mount(<span class="hljs-string">'#app'</span>)
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">body</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">html</span>&gt;</span>
</code></pre>
<p>This will create a working <strong>Tabs</strong> component on your page.</p>
<h2 id="browser-compatibility">Browser compatibility <a class="header-anchor" href="#browser-compatibility" aria-hidden="true">&#128279;</a></h2>
<p>All browsers supported by <a href="https://github.com/vuejs/vue">Vue 2</a> and <a href="https://github.com/twbs/bootstrap">Bootstrap 3 CSS</a> are suppose to be also supported by this lib  (IE8 and below are not supported).</p>
</markdown-wrapper></template><script>export default {components:{}}</script>


